.home-card{
    width: 100%;
    max-width: 930px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}

.card-solo{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 10px;
    border-radius: 20px;
    border: 2px solid rgb(143, 150, 141);
    background-color: rgba(209, 187, 187, 0.664);
    overflow: hidden;
}
.card-solo h1{
    text-align: center;
    margin-top: 0px;
    font-family: 'Roboto';
    /* border: 1px solid red; */
}
.card-solo img{
    margin: 0 auto;
    width: 130px;
    height: 130px;
    transition: all 0.30s;
}
.card-solo img:hover{
    transform: scale(1.6);
    transition: all 0.30s;
}
.card-solo .atk{
    background-color: rgb(85, 236, 55, 0.4);
    box-shadow: 0px 0px 10px rgb(85, 236, 55);
}
.card-solo .def{
    background-color: rgb(241, 52, 52, 0.4);
    box-shadow: 0px 0px 10px rgb(241, 52, 52);
}

.card-solo a{
    width: 100%;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    text-decoration: none;
    color: black;
    background-color: rgb(188, 190, 188);
    

}


.card-solo a:hover{
    transition: cubic-bezier(0.165, 0.84, 0.44, 1) 0.50s;
    background-color: gray;
    color: gold;
    cursor: pointer;
}

.delete{
    position: absolute;
    border: none;
    align-self: flex-end;
    margin-top: 10px;
    margin-left: -10px;
    font-size: 20px;
    font-weight: bolder;
    background-color: transparent;
}
.delete:hover{
    cursor: pointer;
    color: red;
}
@media(max-width: 760px ){
    .card-solo{
      align-items: center;
    }
    .card-solo img{
        margin-top: -10px;
        margin-bottom: 30px;
    }
    .card-solo img:hover{
        transform: scale(1.4);
        transition: all 0.30s;
    }
    .card-solo a{
margin-top: 60px;
    }
    .card-solo .power{
        display: flex;
        flex-direction: row;
        width: 100%;
        justify-content: space-between;
        margin-top: -115px;
        
    }
    .card-solo .power p{
        /* border: 1px solid rebeccapurple; */
        font-size: 20px;
        /* margin-top: -115px; */
    }
    .card-solo .atk{
        padding-left: 10px;
    }
    .card-solo .def{
        padding-right: 10px;
    }
}
