.container-detalhe .imgBack{
    margin: 0;
    padding: 0;
    width: 100%;
}

.container-detalhe .namePoke{
    position: absolute;
    /* border: 1px solid red; */
    margin: 0;
    width: 100%;
    text-align: center;
    font-size: 40px;
    font-family: 'Roboto';
    font-weight: bold;
    letter-spacing: 3px;
    color: black;
}

.container-update{
    position: absolute;
    margin-top: 100px;
}
.container-update label{
    margin-left: 10px;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
}
.container-update input{
    margin-left: 20px;
    padding: 5px 9px;
    border-radius: 20px;
    border: 1px solid black;
    box-shadow: 4px 3px  5px rgba(233, 73, 73, 0.788);
}

.container-update button{
    margin-left: 30px;
    width: 150px;
    margin-top: 10px;
    padding: 5px 9px;
    border-radius: 20px;
    border: 1px solid black;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    cursor: pointer;
}
.container-update button:hover{
    color: black;
    background-color: white;
}

@media(min-width: 700px){
    .container-update{
     
        margin-top: 250px;
    }
    .container-detalhe .imgBack{
        height: 100vh;
    }

}