.main-header{
    background-color: black;
    height: 72px;
}

.header-content{
    width: 100%;
    max-width: 930px;
    height: 72px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content:center;
}

.header-content a{
    /* position: absolute; */
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 25px;
    font-family: 'Roboto';
    padding: 0 40px;
}
.header-content a:hover{
    color: yellow;
}

.logo-red{
    margin-top: 20px;
    padding-left: 10px;
    position: absolute;
    width: 110px;
}